.main-header {
  position: relative;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  z-index: 50;
}

.main-header__overlay {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.main-header__container {
  position: absolute;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  padding-top: 40px;
  background-color: rgba(255, 255, 255, 0);
}
@media (max-width: 1439px) {
  .main-header__container {
    padding-top: 25px;
  }
}
@media (max-width: 1199px) {
  .main-header__container {
    display: flex;
    flex-direction: column;
    padding-top: 30px;
    padding-bottom: 55px;
  }
}
@media (max-width: 767px) {
  .main-header__container {
    padding-top: 15px;
    padding-bottom: 39px;
  }
}

.main-header__logo-container {
  margin-right: 121px;
}
@media (max-width: 1439px) {
  .main-header__logo-container {
    margin-right: 89px;
  }
}
@media (max-width: 1199px) {
  .main-header__logo-container {
    margin: 0 auto;
    z-index: 1;
  }
}
@media (max-width: 767px) {
  .main-header__logo-container {
    margin: 0;
    margin-left: auto;
  }
}

.main-header__container:has(.main-navigation--nojs) {
  position: unset;
  background-color: #ffffff;
}

.main-header__container:has(.main-navigation--opened) {
  position: fixed;
  background-color: #ffffff;
}
.main-header__container:has(.main-navigation--opened) .main-header__logo {
  color: #2d383f;
}

.main-navigation {
  display: grid;
  grid-template-columns: 1fr max-content;
  align-items: center;
}
@media (max-width: 1199px) {
  .main-navigation {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, minmax(44px, auto));
    column-gap: 144px;
    row-gap: 37px;
    width: 100%;
    margin-top: -48px;
  }
}
@media (max-width: 767px) {
  .main-navigation {
    grid-template-rows: repeat(3, minmax(30px, auto));
    column-gap: 70px;
    row-gap: 11px;
    margin-top: -54px;
  }
}

@media (max-width: 1199px) {
  .main-navigation__menu {
    grid-row: 2/-1;
    margin-left: -6px;
  }
}
@media (max-width: 767px) {
  .main-navigation__menu {
    grid-row: 3/-1;
  }
}
@media (max-width: 1199px) {
  .main-navigation__menu ul {
    flex-direction: column;
  }
}
@media (max-width: 1199px) {
  .main-navigation__menu li {
    display: flex;
    padding: 10px;
    padding-bottom: 8px;
    padding-left: 0;
    text-align: left;
  }
}
@media (max-width: 767px) {
  .main-navigation__menu li {
    padding: 7px;
    padding-left: 0;
  }
}
.main-navigation__menu a {
  color: #ffffff;
}
.main-navigation__menu a:hover {
  border-bottom-color: #ffffff;
}
.main-navigation__menu a:focus {
  border-color: #ffffff;
  outline: none;
}
@media (max-width: 1199px) {
  .main-navigation__menu a {
    font-weight: 700;
    letter-spacing: 0.64px;
    text-transform: uppercase;
    color: #2d383f;
  }
  .main-navigation__menu a:hover {
    border-bottom: 1px solid #2d383f;
  }
  .main-navigation__menu a:focus {
    border-color: #2d383f;
  }
}
@media (max-width: 767px) {
  .main-navigation__menu a {
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.56px;
  }
}

.main-navigation__contacts {
  margin-left: 121px;
}
@media (max-width: 1439px) {
  .main-navigation__contacts {
    margin-left: 89px;
  }
}
@media (max-width: 1199px) {
  .main-navigation__contacts {
    grid-column: 2/-1;
    margin-left: 40px;
    text-align: end;
  }
}
@media (max-width: 767px) {
  .main-navigation__contacts {
    grid-row: 2/-1;
    align-self: flex-start;
    margin-left: 0;
  }
}
.main-navigation__contacts a {
  color: #ffffff;
}
.main-navigation__contacts a.disabled {
  opacity: 0.1;
  pointer-events: none;
}
@media (max-width: 1199px) {
  .main-navigation__contacts a {
    font-size: 16px;
    line-height: 16px;
  }
  .main-navigation__contacts a.disabled {
    color: #0266c1;
  }
}
@media (max-width: 767px) {
  .main-navigation__contacts a {
    font-size: 14px;
    line-height: 14px;
  }
}

.main-navigation__toggle {
  position: relative;
  display: none;
  width: 44px;
  height: 44px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
@media (max-width: 1199px) {
  .main-navigation__toggle {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-navigation__toggle {
    width: 30px;
    height: 30px;
  }
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
.main-navigation--nojs a {
  color: #2d383f;
}
.main-navigation--nojs a:not(.main-navigation__phone):hover {
  border-bottom: 1px solid #2d383f;
}
.main-navigation--nojs a:not(.main-navigation__phone):focus {
  border-color: #2d383f;
}

.main-navigation--closed .main-navigation__toggle-icon {
  position: absolute;
  top: 19px;
  left: 0;
  display: block;
  width: 17px;
  height: 2px;
  background-color: #ffffff;
  transition: 0.3s;
}
.main-navigation--closed .main-navigation__toggle-icon::before, .main-navigation--closed .main-navigation__toggle-icon::after {
  position: absolute;
  left: 0;
  width: 26px;
  height: 2px;
  background-color: #ffffff;
  content: "";
}
.main-navigation--closed .main-navigation__toggle-icon::before {
  top: -8px;
  transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
.main-navigation--closed .main-navigation__toggle-icon::after {
  bottom: -8px;
  transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
@media (max-width: 1199px) {
  .main-navigation--closed {
    grid-template-rows: minmax(44px, auto);
  }
  .main-navigation--closed .main-navigation__menu {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-navigation--opened {
    column-gap: 32px;
  }
}
.main-navigation--opened .main-navigation__toggle-icon {
  position: absolute;
  top: 19px;
  left: 0;
  width: 20px;
  height: 2px;
  transition: 0.3s;
}
.main-navigation--opened .main-navigation__toggle-icon::before, .main-navigation--opened .main-navigation__toggle-icon::after {
  position: absolute;
  right: -4px;
  width: 26px;
  height: 2px;
  content: "";
  background-color: #2d383f;
}
.main-navigation--opened .main-navigation__toggle-icon::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}
.main-navigation--opened .main-navigation__toggle-icon::after {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}
.main-navigation--opened .main-navigation__phone {
  color: #2d383f;
}

.socials {
  max-width: 120px;
}
@media (max-width: 1199px) {
  .socials {
    max-width: 130px;
  }
}
.socials ul {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.socials li {
  min-width: 30px;
  min-height: 30px;
  margin-right: 15px;
  margin-bottom: 7px;
}
