  .socials li {
    margin-top: 5px;
    margin-bottom: 0;
  }
}
.socials li:nth-of-type(3n),
.socials li:last-of-type {
  margin-right: 0;
}

.socials__link {
  position: relative;
  display: block;
  min-width: 30px;
  min-height: 30px;
  line-height: 0;
  color: #2d383f;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid rgba(45, 56, 63, 0.3);
}
.socials__link:hover, .socials__link:focus {
  border-color: #2d383f;
  outline: none;
